import logo from './logo.svg';
import './App.css';
import Card from './cards/Card';
import Body from './Body';
import {First, Second, Third} from './cards/pages/Pages';
import {BrowserRouter as Router, Switch, Route,} from "react-router-dom";
import {Context, TextContext} from './Context';

function App() {
  let firstCard = '1'
  let secondCard = '2'
  let thirdCard = '3'
  return (
    <Context>
      <Router>
        <Body>
          <Switch>
            <Route exact path='/' >
              <Card title='Hello im first card' source='https://picsum.photos/420/301' card={firstCard} text='Just some text for first card' />
              <Card title='Hello im second card' source='https://picsum.photos/420/299' card={secondCard} text='Wow this is second card 0_0' />
              <Card title='Hello im third card' source='https://picsum.photos/421/300' card={thirdCard} text='Dont press me :D' />
            </Route>
            <Route path={'/'+firstCard} >
              <First/>
            </Route>
            <Route path={'/'+secondCard} >
              <Second/>
            </Route>
            <Route path={'/'+thirdCard} >
              <Third/>
            </Route>
          </Switch>
        </Body>
      </Router>
    </Context>
  );
}

export default App;
